import {lazy} from 'react'
import Login from '../components/Login'
import AppLayout from '../components/Layout'
import Auth from '../views/System/Auth'
import Test from '../views/System/Test'
import {Navigate} from 'react-router-dom'
import {
    FileOutlined,
    TeamOutlined,
    UserOutlined,
    SettingOutlined,
    MenuOutlined
} from '@ant-design/icons';
const Home=lazy(()=>{return import('../components/Home')})
const Role=lazy(()=>{return import('../views/Role')})
const User=lazy(()=>{return import('../views/User')})
const Menu=lazy(()=>{return import('../views/Menu')})

const router=[
    // These are the same as the props you provide to <Route>
    {
      id:'5',
      element: <AppLayout/>,
      children: [
        { id:'16',path: "/user",  element: <User />,title:'',icon:<UserOutlined/> },
        { 
            id:'17',
            path: "/system", 
            title:'',
            icon: <SettingOutlined/>,
            children: [
                { id:'21',path: "/system/role",  element: <Role />,title:'',icon:<TeamOutlined />},
                { id:'37',path: "/system/menu", element: <Menu />,title:'',icon:<MenuOutlined /> },
            ]
        }
      ]
    },
    { path: "/login", element: <Login /> },
    { id:'4',path: "/system/test/*", element: <Test />,title:'',icon:<TeamOutlined/> },
    { path:"/",element:<Navigate to="/login"/>},
    { path:"*",element:<Navigate to="/user"/>}
]
export default router